.AppLogo {
    animation: App-logo-spin infinite 20s linear;
    height: 15vmin;
    pointer-events: none;
  }
  
.AppHeader {
    min-height: 30vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.AppLink {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  